/* General form styles */
.styled-form {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    background: linear-gradient(135deg, #6a11cb, #2575fc);
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    font-family: Arial, sans-serif;
    color: #fff;
  }
  
  /* Form group styling */
  .form-group {
    margin-bottom: 20px;
  }
  
  /* Label styling */
  .form-group label {
    display: block;
    font-size: 16px;
    margin-bottom: 8px;
  }
  
  /* Input and textarea styling */
  .styled-form input,
  .styled-form textarea {
    width: 90%;
    padding: 10px 15px;
    font-size: 16px;
    border: none;
    border-radius: 6px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background: #fff;
    color: #333;
    outline: none;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  .styled-form input:focus,
  .styled-form textarea:focus {
    box-shadow: 0 0 8px rgba(100, 149, 237, 0.8);
  }
  
  /* Textarea specific styling */
  .styled-form textarea {
    resize: none;
    min-height: 120px;
  }
  
  /* Button styling */
  .btn-submit {
    width: 100%;
    padding: 12px 20px;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    background: #ff6b6b;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease-in-out, transform 0.2s ease-in-out;
  }
  
  .btn-submit:hover {
    background: #ee5253;
    transform: translateY(-2px);
  }
  
  .btn-submit:active {
    background: #d62d2d;
    transform: translateY(0);
  }
  
  /* Add some spacing and alignment */
  .styled-form {
    text-align: left;
    line-height: 1.5;
  }
  