html, body {
  height: 100%;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Make the app container span the full viewport height */
}

.nav-container {
  display: flex;
  justify-content: space-between;
  font-family: Arial, sans-serif;
  align-items: center;
  padding: 10px 20px;
  background-color: #c70000;
  color: #fff;
}

.nav-container a {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

.nav-container img {
  width: 125px;
}

.navHeading {
  margin: 0;
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 1rem;
  align-items: center;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

nav ul li {
  margin: 0 0.4rem;
}

nav ul li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 0.6rem 0.4rem;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

nav ul li a:hover {
  background-color: white;
  color: red;
}

nav ul li a:active {
  background-color: darkred;
  color: white;
}

footer {
  text-align: center;
  padding: 1rem;
  background-color: #c70000;
  color: #fff;
  margin-top: auto;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensure the root element takes up full height */
}

.layout {
  display: flex;
  flex-direction: column;
  flex: 1; /* Make the layout container fill available space */
}

.content {
  flex: 1; /* This makes the content area take up the remaining space */
  padding: 20px;
}

/* Hamburger menu styles */
.hamburger {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

.hamburger span {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 3px 0;
  transition: transform 0.3s ease-in-out;
}

.homepage {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  margin: 20px;
  text-align: justify;
}

.bio-paragraph {
  overflow: hidden;
}

.bio-image {
  float: left;
  margin-right: 15px; /* Adds spacing between the image and the text */
  margin-bottom: 10px; /* Adds spacing below the image */
  width: 150px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.bio-paragraph p {
  margin: 0;
  flex: 1;
}