/* Mobile-specific styles */
@media (max-width: 900px) {
  .navHeading {
    display: none;
  }

  .hamburger {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    z-index: 1000; /* Ensure hamburger stays above other elements */
  }

  .hamburger span {
    width: 25px;
    height: 3px;
    background-color: white;
    margin: 3px 0;
    transition: transform 0.3s ease, opacity 0.3s ease;
  }

  nav ul {
    display: flex;
    flex-direction: column;
    position: fixed; /* Stick menu to the bottom of the viewport */
    bottom: -100%; /* Initially hidden below the viewport */
    left: 0;
    width: 100%;
    background-color: #c70000;
    padding: 10px 0;
    margin: 0;
    opacity: 0;
    pointer-events: none;
    transition: bottom 0.3s ease, opacity 0.3s ease;
    z-index: 999; /* Keep menu above other elements */
  }

  nav ul.open {
    bottom: 0; /* Slide into view */
    opacity: 1;
    pointer-events: auto;
  }

  nav ul li {
    text-align: center;
    margin: 0.5rem 0;
  }

  nav ul a {
    display: block;
    color: white;
    text-decoration: none;
    font-size: 18px;
    padding: 10px 20px;
    border: 2px solid white;
    border-radius: 5px;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
  }

  nav ul a:hover {
    background-color: white;
    color: #c70000;
    transform: scale(1.05);
  }

  /* Hamburger active state */
  .hamburger.active span:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }

  .hamburger.active span:nth-child(2) {
    opacity: 0;
  }

  .hamburger.active span:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
  }
}
